import React, { FC } from 'react';
import { useSelector } from 'react-redux';
import {
  boolColumn,
  CellEditProps,
  ClickEnum,
  ColumnsProps,
  DefaultSortProps,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchExpression,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasGrupoEmpresaConfig } from '../../../../dependencies/grupoEmpresaConfigDependency';
import { CentroCusto, CLASS_NAME } from '../../../../models/dtos/ger/centroCusto';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'codigo',
    order: OrderType.ASC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const CentroCustoSearchView: FC<SearchViewProps<CentroCusto>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const columns: ColumnsProps[] = [
    {
      dataField: 'codigo',
      editable: false,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Código',
    },
    {
      dataField: 'codigoReduzido',
      editable: false,
      filter: {
        type: FilterType.NUMBER,
      },
      sort: true,
      text: 'C.R.',
    },
    {
      dataField: 'descricao',
      editable: true,
      filter: {
        type: FilterType.TEXT,
      },
      sort: true,
      text: 'Descrição',
    },
    boolColumn({
      dataField: 'analitica',
      editable: false,
      text: 'Analítica',
    }),
  ];

  function getSearchExpression(): SearchExpression[] {
    return [
      {
        fieldName: 'grupoCentroCusto.id',
        value: globalReducer.globalParameter.grupoEmpresaConfig.grupoCentroCusto.id,
      },
    ];
  }

  return (
    <WindowComponent
      title="Manutenção de Centros de Custo"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasGrupoEmpresaConfig(globalReducer.globalParameter)]}
      wikiId={4415}
    >
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, 'CentroCustoEditView', CLASS_NAME)}
        searchExpressions={getSearchExpression}
      />
    </WindowComponent>
  );
};

export default CentroCustoSearchView;
