import { GlobalParameter } from '../models/dtos/ger/globalParameter';

export function hasGrupoEmpresaConfig(globalParameter: GlobalParameter): string | undefined {
  let result = null;

  const msg: string =
    'O Grupo de empresas não está configurado para a empresa selecionada. Para configurar acesse:\n' +
    'Modulo Principal > Manutenção > Configurações > Grupo de Empresa';

  if (
    !globalParameter.empresa ||
    !globalParameter.empresa.grupoEmpresaRaiz.grupoEmpresaConfig ||
    !globalParameter.empresa.grupoEmpresa.grupoEmpresaConfig
  ) {
    result = msg;
  }

  return result;
}
